import utilStyles from '../../styles/utils.module.css';
import Toggle from '../toggle/index';
import Link from 'next/link'
import headerStyle from './header.module.css';

const name = 'Personal blog by '
const desc = "Let's discuss almost everything"

export default function Header({ theme, setTheme }) {
    
    return (
        <header className={headerStyle.header}>
            <Link href="/">
                <img
                    src={`/images/avatar.jpg`}
                    className={`${headerStyle.headerImage} ${utilStyles.borderCircle}`}
                    alt={name}
                />
            </Link>  
            <div className={headerStyle.header_right_side}>
                <div className={`${utilStyles.ml8} ${headerStyle.blog_description}`}>
                    <div className={`${utilStyles.headingLw} ${theme ? utilStyles.whiteText : null}`}>
                        {name}
                        <a className={`${theme ? 'dark' : null} ${headerStyle.linkToInstagram}`} target="_blank" href="https://www.instagram.com/temirtator23/">
                            Shagyrov Temirlan
                        </a>
                    </div>
                    <div className={utilStyles.description}>{desc}</div>
                </div>
                <Toggle
                    selected={theme}
                    toggleSelected={() => {
                        setTheme(!theme);
                        localStorage.setItem('theme', !theme ? 'dark' : 'light');
                    }}
                />
            </div>
        </header>      
    );
};